import React from 'react'
import { Card } from './card.js'
import Functions from '../services/functions.js'

export const List = () => {

  return (
    <button onClick={Functions.test}>Clicca qui</button>
  );
  const numeri = [1, 2, 3]
  const lista = numeri.map((numero) => {
    return (
      <Card>{numero}</Card>
    )
  })

  return (
    <div>
      {lista}
    </div>
  )
}